.menu-button {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
}

.menu-button__wrapper {
  width: 25px;
  height: 20px;
  position: relative;
}

.menu-button__line {
  transform-origin: 0;
  background-color: #f1f5f4;
  width: 100%;
  height: 2px;
  transition: all .3s;
  position: absolute;
}

.menu-button__line--top {
  top: 0;
  left: 0;
}

.menu-button__line--center {
  top: 50%;
  transform: translateY(-50%);
}

.menu-button__line--bottom {
  bottom: 0;
  left: 0;
}

.menu-button--close .menu-button__line--top {
  top: -10%;
  left: 10%;
  transform: rotate(45deg)scale(1.15);
}

.menu-button--close .menu-button__line--center {
  opacity: 0;
}

.menu-button--close .menu-button__line--bottom {
  bottom: -5%;
  left: 10%;
  transform: rotate(-45deg)scale(1.15);
}

